.blog {
  &__gallery {
    display: flex;
  }
  &__subscribe {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 24px;
    @include tablet {
      gap: 32px;
    }
  }
}
.swiper__button {
  display: flex;
  flex-direction: row;
  justify-content: center;
  &--prev {
    width: 50px;
    height: 50px;
    @include tablet {
      width: 61px;
      height: 61px;
    }
  }
}
